<template>
  <div class="right-content" :key="instanceKey">
    <div class="top-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`计划完成`">
          <a-select
            v-model:value="formState.planSelectType"
            :options="selectTypeData"
            @change="handlePlanTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <div style="width: 100%; height: 1.85rem">
          <div style="margin-left: 0.16rem; transform: scale(0.85); transform-origin: 0 0">
            <EchartLineBarChart
              :data="barChartData"
              :colors="barChartData.colors"
              :width="`26.06vw`"
              :height="tabStatus === 'on' ? `28.07vh` : `31.38vh`"
              :category="barChartData.categories"
              ybgcolor="#01B7D730"
              :name="barChartData.seriesName"
              :rotate="0"
              @click-item="planPowerToBiReport"
              xAxisLabelInterval="auto"
              :dataZoomDefaultValueRange="barChartData.dataZoomDefaultValueRange"
              :dataZoomDisabled="false"
              :dataZoomMaxValueSpan="barChartData.categories.length"
            />
          </div>
        </div>
      </div>
    </div>
    <div class="middel-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`电站分布`" />
        <div style="width: 100%; height: 1.85rem">
          <BubbleChart
            v-if="chartData.length"
            :data="chartData"
            :width="200"
            :height="180"
            :font-size="fontSize"
            :font-color="fontColor"
            :bubble-color="bubbleColor"
            style="margin-top: 2.4vh"
          />
        </div>
      </div>
    </div>

    <div class="bottom-section section-box">
      <div class="section-bg">
        <SubtitleBar :subtitle="`运维人员`">
          <a-select
            v-model:value="formState.selectType"
            :options="selectPersonTypeData"
            @change="handleTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>

        <div style="width: 100%">
          <!-- <DonutChart :title="donutTitle" 
            :data="donutData.pieData" 
            width="26.67vw" 
            :height="tabStatus==='on'?'24.97vh':'27.37vh'" 
            radius="100" 
            @click-item="personToBiReport" 
          /> -->
          <!-- <TreeDPieChart
            v-if="donutData.pieData"
            :data="donutData.pieData"
            :colors="donutData.colors"
            :width="250"
            :height="220"
            :alpha="22"
            unit="人"
          /> -->

          <EchartPieChart
            id="person-stat-pie"
            :width="`24.4vw`"
            :height="tabStatus === 'on' ? `33.08vh` : `26.53vh`"
            :data="donutData.pieData"
            :roseType="false"
            :radius="tabStatus === 'on' ? ['35%', '50%'] : ['40%', '60%']"
            showLabel
            :pieCenter="tabStatus === 'on' ? ['50%', '40%'] : ['50%', '55%']"
            @click-item="personToBiReport"
          />
        </div>
        <!-- <div :style="`width: 100%; height: ${tabStatus==='on'?'23.72vh':'26.47vh'};display:flex;align-items:center;`">
          <NtgaleChart v-if="donutData.pieData" id="operation-person-pie" :width="`11.84vw`" :height="tabStatus==='on'?`19.5vh`:`22.22vh`" :data="donutData.pieData" :roseType="false"
            :radius="['30%', '78%']" showLegend legendPosition="right" />

        </div> -->
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { ref, reactive, UnwrapRef, onMounted, watch, getCurrentInstance } from 'vue';
  import { assign } from 'min-dash';
  import SubtitleBar from '/@/components/Framework/Chart/SubtitleBar.vue';
  import EchartLineBarChart from '/@/components/Framework/Chart/EchartLineBarChart.vue';
  import BubbleChart from '/@/components/Framework/Chart/BubbleChart.vue';
  import * as OverviewApi from '@/api/da/cockpit/overview';
  import { addTabPage } from '@/utils/route';
  import { buildUUID } from '@/utils/uuid';
  import EchartPieChart from '/@/components/Framework/Chart/EchartPieChart.vue';
  // import TreeDPieChart from '/@/components/Framework/Three/TreeDPieChart.vue';
  // import TreeDPie from '/@/components/Framework/Three/TreeDPie.vue';
  // import NtgaleChart from '/@/components/Framework/Chart/NtgaleChart.vue';
  // import DonutChart from '/@/components/Framework/Chart/DonutChart.vue';

  //搜索参数
  const searchInfo: UnwrapRef<any> = reactive({});
  const instanceKey = ref(buildUUID());
  const props = defineProps({
    searchInfoByTime: { type: Object, default: {} },
    tabStatus: { type: String, default: '' },
  });

  watch(
    () => props.searchInfoByTime,
    (value) => {
      assign(searchInfo, value);
      doOperationPerson(formState.selectType);
    },
  );

  const formState = reactive({
    planSelectType: 'all',
    selectType: 'ageRange',
  });

  onMounted(async () => {
    doPowerStatByPlan();
    doProvinceStat();
  });

  // ==================================计划完成 ======================================
  const selectTypeData: any = ref([
    { label: '历年', value: 'all' },
    { label: '当年', value: 'inThoseYear' },
    { label: '近12个月', value: 'december' },
  ]);

  const barChartData = ref({
    barData: [[], []],
    categories: [],
    lineData: [[]],
    colors: [
      ['#488FF6', '#60D1F3'],
      ['#078C5D', '#68E4B8'],
      ['#078C5D', '#68E4B8'],
      ['#e59837', '#fae895'],
      ['#078C5D', '#68E4B8'],
    ],
    seriesName: [['减线损上网电量', '计划电量'], ['完成率']],
    dataZoomDefaultValueRange: [0, 4],
  });
  const doPowerStatByPlan = async () => {
    const dayParams = {
      dateType: formState.planSelectType,
    };
    barChartData.value.barData.splice(0, barChartData.value.barData.length);
    barChartData.value.categories.splice(0, barChartData.value.categories.length);
    barChartData.value.lineData.splice(0, barChartData.value.lineData.length);

    const powerStatByPlan = await OverviewApi.powerStatByPlan(dayParams);
    if (powerStatByPlan) {
      if (powerStatByPlan.barData?.length) {
        barChartData.value.barData = powerStatByPlan.barData;
      } else {
        barChartData.value.barData = [];
      }
      if (powerStatByPlan.lineData?.length) {
        barChartData.value.lineData[0] = powerStatByPlan.lineData;
      } else {
        barChartData.value.lineData = [];
      }
      barChartData.value.categories = powerStatByPlan.categories;
    }
  };
  const handlePlanTypeChange = () => {
    doPowerStatByPlan();
  };

  // ==================================电站分布 begin ======================================
  const fontSize = ref('0.18rem');
  const fontColor = 'white';
  const bubbleColor = '#1890FF';

  const colorData = ['#1890FF', 'green', 'yellow', 'blue', 'skyblue', 'purple', '#9023a0'];
  const chartData = ref([]);

  const doProvinceStat = async () => {
    chartData.value.splice(0, chartData.value.length);
    const provinceStat = await OverviewApi.provinceStat({});
    if (provinceStat) {
      provinceStat.forEach((item, index) => {
        let _color = colorData[index];
        if (!_color) {
          var random1 = Math.floor(Math.random() * colorData.length);
          _color = colorData[random1];
        }
        chartData.value.push({
          name: item.provinceShortName,
          label: item.provinceShortName,
          value: item.stationCount,
          size: item.stationCount,
          color: _color,
          unit: '座',
        });
      });
    }
  };

  // ==================================运维人员 begin ======================================
  const donutTitle = ref('总人数');
  const selectPersonTypeData: any = ref([
    { label: '年龄结构', value: 'ageRange' },
    { label: '学历结构', value: 'degree' },
  ]);

  const donutData = reactive({ pieData: null });
  const doOperationPerson = async (value) => {
    donutData.pieData = null;
    const callback = (item) => {
      return { name: item.label, value: item.value };
    };
    const colors = ['#03AAFF90', '#62E99F90', '#99DD0090', '#00BBFF90'];
    const data = await OverviewApi.operationPerson({
      startMonth: searchInfo.startMonth,
      endMonth: searchInfo.endMonth,
    });
    if (data) {
      if (value == 'ageRange') {
        donutData.pieData = data.ageRange.map(callback);
        donutData.colors = colors;
      } else {
        donutData.pieData = data.degree.map(callback);
        donutData.value.colors = colors;
      }
    }
  };
  const handleTypeChange = (value) => {
    doOperationPerson(value);
  };

  //==================================穿透方法======================================
  function planPowerToBiReport(params) {
    addTabPage('/bi/elec/planned', '计划电量统计', {
      startDate: formState.planSelectType === 'all' ? params.name + '-01' : params.name,
      endDate: formState.planSelectType === 'all' ? params.name + '-12' : params.name,
    });
  }
  const personToBiReport = () => {
    if (formState.selectType === 'ageRange') {
      //年龄
      addTabPage('/bi/integrated/personnelStructure');
    }
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceKey.value = buildUUID();
  };

  defineExpose({
    handleUpdate,
  });
</script>

