    display: flex;
    position: absolute;
    z-index: 99;
    top: 0.9rem;
    right: 0;
    flex-direction: column;
    align-items: flex-end;
    width: 25vw;
    height: calc(100vh - 0.9rem);
    padding: 0 0.68vw 1.68vh 0.57vw;

    .section-box {
      flex: 1;
      width: 100%;
      height: 33.3333%;
      overflow: hidden;

      .section-bg {
        width: 100%;
